import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		trackQueue: JSON.parse(localStorage.getItem("trackQueue") || "[]"),
		trackSources: [],
		currentTrackId: localStorage.getItem('currentTrackId'),
		fileLocation: '',
		autoPlayStatus: Boolean(localStorage.getItem('autoPlayStatus')),
		stop: false,
	},
	getters: {
		currentTrack: state => {
			for (let i = 0; i < state.trackSources.length; i++) {
				var e = state.trackSources[i];
				if(e.slug == state.currentTrackId){
					return e;
				}
			}
			return e;
		}
	},
	mutations: {

		nextTrack: (state, payload) => {
			if(state.trackQueue.length > 1){
				let nextTrack = state.trackQueue.indexOf(payload.currentTrack) + 1;
				let firstTrack = state.trackQueue[0];

				if(state.trackQueue.length == nextTrack){
					localStorage.setItem('currentTrackId', firstTrack.slug);
					state.currentTrackId = firstTrack.slug;
				}else{
					localStorage.setItem('currentTrackId', state.trackQueue[nextTrack].slug);
					state.currentTrackId = state.trackQueue[nextTrack].slug;
				}
				state.trackSources.length = 0;
				state.trackQueue.forEach(el => {
					state.trackSources.push(el);
				});
			}
		},
		prevTrack: (state, payload) => {
			if(state.trackQueue.length > 1){
				let prevTrack = state.trackQueue.indexOf(payload.currentTrack) - 1;
				let lastTrack = state.trackQueue[state.trackQueue.length - 1];
				if(prevTrack == -1){
					localStorage.setItem('currentTrackId', lastTrack.slug);
					state.currentTrackId = lastTrack.slug;
				}else{
					localStorage.setItem('currentTrackId', state.trackQueue[prevTrack].slug);
					state.currentTrackId = state.trackQueue[prevTrack].slug;
				}
				state.trackSources.length = 0;
				state.trackQueue.forEach(el => {
					state.trackSources.push(el);
				});
			}
		},
		selectTrack: (state, e) => {
			state.trackSources.length = 0;
			for (let i = 0; i < state.trackQueue.length; i++) {
				const el = state.trackQueue[i];
				state.trackSources.push(el);
				if(el.slug == e){
					localStorage.setItem('currentTrackId', el.slug);
					state.currentTrackId = el.slug
				}
			}
		},
		removeTrack: (state, payload) => {
			let targetTrack = state.trackQueue.indexOf(payload.e);
			let currentTrack = payload.currentTrack;
			let firstTrack = state.trackQueue[0];
			let nextTrack = state.trackQueue.indexOf(payload.currentTrack);

			state.trackQueue.splice(targetTrack, 1);
			localStorage.setItem("trackQueue", JSON.stringify(state.trackQueue));

			// Start next or go top if current track removed.
			if(currentTrack.slug == payload.e.slug){
				if(targetTrack == state.trackQueue.length){
					localStorage.setItem('currentTrackId', firstTrack.slug);
					state.currentTrackId = firstTrack.slug;
				}else{
					localStorage.setItem('currentTrackId', state.trackQueue[nextTrack].slug);
					state.currentTrackId = state.trackQueue[nextTrack].slug;
					console.log(state.trackQueue[nextTrack].slug)
				}

				if(state.trackQueue.length == 0){
					state.stop = true;
					var trackQueueBlank = [{file: "/media/blank.mp3", slug: "blank"}]
					localStorage.setItem("trackQueue", JSON.stringify(trackQueueBlank));
					state.trackQueue = JSON.parse(localStorage.getItem("trackQueue"));
					state.trackSources = trackQueueBlank;
					localStorage.setItem('currentTrackId', 'blank');
					state.currentTrackId = "blank";
				}
				state.trackSources.length = 0;
				state.trackQueue.forEach(el => {
					state.trackSources.push(el);
				}); 
			}

			// Clear upper track and get up other track.
			/* else if(state.currentTemp > e){
				state.currentTemp--;
			} */
		},
		addQueue: (state, e) => {
			let checkStatus = false;
			for (let i = 0; i < state.trackQueue.length; i++) {
				const el = state.trackQueue[i];
				if(el.slug == e.slug){
					checkStatus = true
				}
				if(el.slug == 'blank'){
					state.stop = false;
					state.trackQueue.splice(0, 1);
				}
			}
			checkStatus ? '' : state.trackQueue.push(e);

			localStorage.setItem("trackQueue", JSON.stringify(state.trackQueue));
		},
		clearQueue: state => {
			state.stop = true;
			var trackQueueBlank = [{file: "/media/blank.mp3", slug: "blank"}]
			localStorage.setItem("trackQueue", JSON.stringify(trackQueueBlank));
			state.trackQueue = JSON.parse(localStorage.getItem("trackQueue"));
			state.trackSources = trackQueueBlank;
			localStorage.setItem('currentTrackId', 'blank');
			state.currentTrackId = 'blank'
		},
		playAll: (state, allTracks) => {
			
			for (let i = 0; i < allTracks.length; i++) {
				const newTrack = allTracks[i];
				for (let i = 0; i < state.trackQueue.length; i++) {
					const el = state.trackQueue[i];
					if(el.slug == newTrack.slug){
						state.trackQueue.splice(i, 1);
					}
					if(el.file == 'blank'){
						state.stop = false;
						state.trackQueue.splice(0, 1);
					}
				}
				state.trackQueue.push(newTrack);
			}
			localStorage.setItem("trackQueue", JSON.stringify(state.trackQueue));
		},
	},
	actions: {
		nextTrack: (context) => {
			context.commit('nextTrack', {currentTrack: context.getters.currentTrack})
		},
		prevTrack: (context) => {
			context.commit('prevTrack', {currentTrack: context.getters.currentTrack})
		},
		selectTrack: (context, e) => {
			context.commit('selectTrack', e)
		},
		removeTrack: (context, e) => {
			context.commit('removeTrack',  {e:e, currentTrack: context.getters.currentTrack})
		},
		addQueue: (context, e) => {
			context.commit('addQueue', e)
		},
		clearQueue: (context) => {
			context.commit('clearQueue')
		},
		playAll: (context, allTracks) => {
			context.commit('playAll', allTracks)
		},
	},
	modules: {
	}
})
