<template>
	<!-- Queue Area -->
	<div class="queue-area">
		<!-- Queue Area Title -->
		<div class="queue-title-area">
			<div>
				<span class="queue-title">Queue</span>
				<span class="iconify" data-icon="simple-line-icons:refresh"></span>
				<span class="iconify" data-icon="simple-line-icons:shuffle"></span>
			</div>
			<div>
				<span class="pointer" @click='clearQueue()'>Clear</span>
				<span class="btn" @click='showSaveModel = true'>Save</span>
			</div>
			<div class="save-model" v-if="showSaveModel">
				<input type="text" v-model="playListName">
				<button class="" @click="savePlayList()">Save</button>
				<button class="" @click="showSaveModel = false"><span class="iconify" data-icon="cil:x" data-inline="false"></span></button>
				<div class="successMsg" v-if="showSuccessMsg">
					Playlist Saved!
				</div>
			</div>
		</div>

		<!-- Queue Songs Items -->
		<div class="queue-item-area" id="queue-item-area">
			<div class="queue-item" v-for="(item, index) in currentQueue"
			:index="index" :key="item.slug" 
			:class="{ active: currentTrackItemId == item.slug, hide: item.slug == 'blank' }" :id="item.slug">
				<div class="queue-head" @click="selectTrack(item.slug)">
					<img :src="'/media/'+item.album_cover" alt=""><div class="queue-head-content"><h4>{{item.title}}</h4><span>{{item.album}}</span></div>
				</div>
				<div class="queue-action">
					<div class="dropdown">
						<span 
							class="iconify dropdown-toggle" 
							data-icon="simple-line-icons:options" 
							id="dropdownMenuButton" 
							data-toggle="dropdown" 
							aria-haspopup="true" 
							aria-expanded="false"
						></span>
						<div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
						<div class="dropdown-item" @click="removeTrack(item)">
							<span class="iconify" data-icon="simple-line-icons:close"></span> Remove from queue
						</div>
						<a class="dropdown-item" href="#"><span class="iconify" data-icon="simple-line-icons:heart"></span> Add to favourite </a>
						<a class="dropdown-item" href="#"><span class="iconify" data-icon="simple-line-icons:playlist"></span> Add to playlist</a>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>

</template>

<script>
import { bus } from '@/main'
export default {
	name: 'Queue',
	props: {
		
	},
	data: function (){
		return{
			showSaveModel: false,
			showSuccessMsg: false,
			playListName: '',
		}
	},
	methods: {
		selectTrack: function(e){
			this.$store.dispatch('selectTrack', e);
			bus.$emit('scrollToActive');
		},
		removeTrack: function(e){
			this.$store.dispatch('removeTrack', e)
		},
		clearQueue: function(){
			this.$store.dispatch('clearQueue');
		},
		savePlayList: function(){
			if(this.playListName){
				localStorage.setItem('pl_mb:'+this.playListName, JSON.stringify(this.$store.state.trackSources));
				this.playListName = '';
				this.showSuccessMsg = true;
				setTimeout(() => {
					this.showSaveModel= false;
					this.showSuccessMsg= false;
				}, 2000);
			}
		}
	},
	computed: {
		currentQueue(){
			return this.$store.state.trackQueue;
		},
		currentTrackItemId() {
			return this.$store.state.currentTrackId
		},
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.queue-item .queue-head{
	cursor: pointer;
}
</style>
